import InputArea from "../../plugin/components/InputArea";

export default {
  name: "whiteList",
  components: {
    InputArea,
  },
  data() {
    return {
      currentPage: 1,
      pagesize: 10,
      total: 0,
      dialogTitle: "",
      formLabelWidth: "120px",
      dialogFormVisible: false,
      dialogVisible: false,
      tableData: [],
      form: {
        versionUpd: { radio: "allVersion" },
        channelUpd: { radio: "allVersion" },
        forbidUpdVersion: { radio: "none" },
        forbidUpdChannel: { radio: "none" },
        isPublic: { radio: "none" },
        updStatus: { radio: "none" },
        checkList: ["android", "ios", "windows"],
        updateTime: "",
        version: "",
        update_type: "",
        update_way: ""
      },
      fileList: [],
      imageUrl: "",
    };
  },
  methods: {
    resetForm() {
      this.form = {
        channel_name: "",
        channel_id: "",
        belong_company: ""
      };
    },

    getChannelList() {
      this.$http
        .get("v1/backend/operation/channels", {
          params: {
            page: this.currentPage,
            limit: this.pageSize,
            company: "",
            channel_name: ""
          }
        })
        .then(res => {
          console.log(res);
          if (res.data.code === 200) {
            this.records = res.data.data;
            this.total = res.data.total;
          }
        });
    },
    //获取公司列表
    getCompanyList() {
      this.$http.get("v1/backend/operation/channel/company").then(res => {
        console.log(res);
        if (res.data.code === 200) {
          this.companyList = res.data.data;
        }
      });
    },
    openAddDialog() {
      this.resetForm();
      this.dialogTitle = "添加渠道";
      this.dialogFormVisible = true;
    },
    addCompany() {
      let data = {
        name: this.form1.company_name
      };
      this.$http
        .post("v1/backend/operation/channel/company", data)
        .then(res => {
          console.log(res);
          if (res.data.code === 200) {
            this.dialogVisible = false;
            this.$message({
              type: "success",
              message: res.data.msg
            });
          }
        });
    },
    addChannel() {
      if (!this.form.id) {
        let data = {
          channel_name: this.form.channel_name,
          channel_num: this.form.channel_id,
          company: this.form.belong_company
        };
        this.$http.post("v1/backend/operation/channels", data).then(res => {
          console.log(res);
          if (res.data.code === 200) {
            this.dialogFormVisible = false;
            this.getChannelList();
            this.$message({
              type: "success",
              message: res.data.msg
            });
          }
        });
      } else {
        console.log("wojinolei");
        let data = {
          channel_name: this.form.channel_name,
          channel_num: this.form.channel_id,
          company: this.form.belong_company,
          channel_id: this.form.id
        };
        this.$http.put("v1/backend/operation/channels", data).then(res => {
          console.log(res);
          if (res.data.code === 200) {
            this.dialogFormVisible = false;
            this.getChannelList();
            this.$message({
              type: "success",
              message: res.data.msg
            });
          }
        });
      }
    },
    /**搜索*/
    search() {},
    handleEdit(index, row) {
      console.log(index, row);
      this.dialogTitle = "编辑渠道";
      this.dialogFormVisible = true;
      this.form.id = row.id;
      this.form.channel_name = row.name;
      this.form.channel_id = row.code;
      this.form.belong_company = row.cname;
    },
    handleDelete(index, row) {
      console.log(row);
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$http
            .delete("v1/backend/operation/channels", {
              params: {
                channel_id: row.id
              }
            })
            .then(res => {
              console.log(res);
              if (res.data.code === 200) {
                this.getChannelList();
                this.$message({
                  type: "success",
                  message: res.data.msg
                });
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: res.data.msg
          });
        });
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.pageSize = val;
      this.getChannelList();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.currentPage = val;
      this.getChannelList();
    },
    handleAvatarSuccess(res, file) {},
    handleChange(file, fileList, info) {
      this.fileList[info] = fileList;
    },
    beforeUpload(file) {},
    uploadFile() {
      let formData = new FormData();
      this.fileList.forEach(item => {
        formData.append("filename", item.raw);
        formData.append("types", 1);
      });
      this.$http.post("v1/backend/upload", formData).then(res => {
        if (res.data.code === 1) {
          this.imageUrl = res.data.path;
        }
      });
    }
  },
  mounted() {
    this.getChannelList();
    this.getCompanyList();
  }
};



